import ProjectCard from '@itcode/molecule/ProjectCard';
import { ProjectType, useProjectState } from '@itcode/tools/store';

import Grid from '@mui/material/Grid';
import { motion } from 'framer-motion';
import { useCallback } from 'react';

export interface ProjectFeedProps
{
	/**
	 * 프로젝트 리스트
	 */
	list: Queries.GetAllProjectQuery;
}

/**
 * 프로젝트 피드 organism 컴포넌트 JSX 반환 메서드
 *
 * @param {ProjectFeedProps} param0: ProjectFeedProps 객체
 *
 * @returns {JSX.Element} JSX
 */
export default function ProjectFeed({ list: { allMarkdownRemark: { edges } } }: ProjectFeedProps): JSX.Element
{
	const { setProject } = useProjectState();

	const images = useCallback((images: readonly (string | null)[] | null | undefined) => images?.reduce<string[]>((acc, cur) =>
	{
		// 유효한 이미지일 경우
		if (cur !== null)
		{
			acc.push(cur);
		}

		return acc;
	}, []) || [], []);

	const getDuring = useCallback((created?: number | null, completed?: number | null) =>
	{
		const start = created || new Date().getTime();
		const end = completed || new Date().getTime();

		return Math.ceil((end - start) / 86400000);
	}, []);

	const handleClick = useCallback((project: ProjectType) =>
	{
		setProject(project);
	}, []);

	return (
		<Grid className='fullwidth' spacing={1} container>
			{edges.map(({ node: { frontmatter, id } }) => (
				<Grid
					key={id}
					md={3}
					xl={4}
					xs={6}
					item
				>
					<motion.div
						className='fullheight'
						initial='initial'
						transition={{ duration: 0.3, type: 'spring' }}
						viewport={{ once: true }}
						whileInView='view'
						variants={{
							initial: { opacity: 0, scale: 0 },
							view: { opacity: 1, scale: 1 }
						}}
					>
						<ProjectCard
							author={frontmatter?.author || ''}
							during={getDuring(frontmatter?.created, frontmatter?.completed)}
							icon={frontmatter?.icon || ''}
							images={images(frontmatter?.images)}
							isProcessing={!frontmatter?.completed}
							title={frontmatter?.title || ''}
							onClick={(): void => handleClick(frontmatter)}
						/>
					</motion.div>
				</Grid>
			))}
		</Grid>
	);
}