import { useThemeState } from '@itcode/tools/store';

import ArrowUpward from '@mui/icons-material/ArrowUpward';
import Fab from '@mui/material/Fab';
import Stack from '@mui/material/Stack';
import classNames from 'classnames/bind';
import { motion } from 'framer-motion';
import { useCallback } from 'react';

import * as styles from './TopSwitch.module.scss';

const cn = classNames.bind(styles);

/**
 * 탑 스위치 organism 컴포넌트 JSX 반환 메서드
 *
 * @returns {JSX.Element} JSX
 */
export default function TopSwitch(): JSX.Element
{
	const { theme } = useThemeState();

	const handleUpClick = useCallback(() =>
	{
		window.scrollTo({ behavior: 'smooth', top: 0 });
	}, []);

	return (
		<Fab className={cn('fab', theme)} data-component='TopSwitch' size='small' onClick={handleUpClick}>
			<motion.div className='fullwidth fullheight' whileTap={{ translateY: -50 }}>
				<Stack alignItems='center' height='100%' justifyContent='center' width='100%'>
					<ArrowUpward color='primary' />
				</Stack>
			</motion.div>
		</Fab>
	);
}