import DevStackCard from '@itcode/molecule/DevStackCard';
import { DevStacks } from '@itcode/tools/env';

import Grid from '@mui/material/Grid';
import { motion } from 'framer-motion';

export interface DevStackFeedProps
{
	/**
	 * 개발 스택 리스트
	 */
	stacks: DevStacks[];
}

/**
 * 개발 스택 피드 organism 컴포넌트 JSX 반환 메서드
 *
 * @param {DevStackFeedProps} param0: DevStackFeedProps 객체
 *
 * @returns {JSX.Element} JSX
 */
export default function DevStackFeed({ stacks }: DevStackFeedProps): JSX.Element
{
	return (
		<motion.div
			className='fullwidth'
			data-component='DevStackFeed'
			exit={{ scale: 0 }}
			initial='initial'
			transition={{ staggerChildren: 0.1 }}
			viewport={{ once: true }}
			whileInView='view'
		>
			<Grid spacing={1} container>
				{stacks.map(({ name, icon }) => (
					<Grid
						alignItems='center'
						justifyContent='center'
						key={name}
						md={3}
						sm={4}
						xs={6}
						item
					>
						<motion.div
							transition={{ duration: 0.3, type: 'spring' }}
							variants={{
								initial: { scale: 0 },
								view: { scale: 1 }
							}}
						>
							<DevStackCard image={icon} name={name} />
						</motion.div>
					</Grid>
				))}
			</Grid>
		</motion.div>
	);
}