import Box, { BoxProps } from '@mui/material/Box';
import Paper from '@mui/material/Paper';
import Stack from '@mui/material/Stack';

export type LogoProps = BoxProps

/**
 * 로고 atom 컴포넌트 JSX 반환 메서드
 *
 * @param {LogoProps} param0: LogoProps 객체
 *
 * @returns {JSX.Element} JSX
 */
export default function Logo({ ...props }: LogoProps): JSX.Element
{
	return (
		<Box data-component='Logo' {...props}>
			<Paper className='fullwidth fullheight' elevation={4}>
				<Stack alignItems='center' height='100%' justifyContent='center' padding={0.5} width='100%'>
					<img alt='logo' height='100%' src='/logo-transparent.png' width='100%' />
				</Stack>
			</Paper>
		</Box>
	);
}