/**
 * 연구 박스 organism 컴포넌트
 *
 * @author RWB
 * @since 2023.07.06 Thu 01:21:53
 */

import { useGetProceedProject } from '@itcode/graphql';
import ProjectFeed from '@itcode/organism/global/ProjectFeed';

import Button from '@mui/material/Button';
import Stack from '@mui/material/Stack';
import Typography from '@mui/material/Typography';
import { Link } from 'gatsby';

/**
 * 연구 박스 organism 컴포넌트 JSX 반환 메서드
 *
 * @returns {JSX.Element} JSX
 */
export default function ResearchBox(): JSX.Element
{
	const list = useGetProceedProject();

	return (
		<Stack alignItems='center' data-component='ResearchBox' spacing={10} width='100%'>
			<Typography fontWeight='bold' letterSpacing={10} variant='h4'>진행중인 프로젝트</Typography>

			<ProjectFeed list={list} />

			<Stack alignItems='center' display='inline-flex' spacing={2}>
				<Typography variant='h5'>모든 프로젝트를 보고싶다면?</Typography>

				<Link to='/projects'>
					<Button size='large'>
						<Typography variant='h5'>전 체 보 기</Typography>
					</Button>
				</Link>
			</Stack>
		</Stack>
	);
}