/**
 * 개발 스택 박스 organism 컴포넌트
 *
 * @author RWB
 * @since 2023.07.28 Fri 01:50:02
 */

import LottieText from '@itcode/molecule/LottieText';
import DevStackFeed from '@itcode/organism/home/DevStackFeed';
import { useGetDevStacks } from '@itcode/tools/api';
import { DevStackCategory } from '@itcode/tools/env';

import AutoStories from '@mui/icons-material/AutoStories';
import Code from '@mui/icons-material/Code';
import DataObject from '@mui/icons-material/DataObject';
import Inventory from '@mui/icons-material/Inventory';
import Storage from '@mui/icons-material/Storage';
import Stack from '@mui/material/Stack';
import Typography from '@mui/material/Typography';
import classNames from 'classnames/bind';
import { useCallback } from 'react';

import * as styles from './DevStackBox.module.scss';

const cn = classNames.bind(styles);

/**
 * 개발 스택 박스 organism 컴포넌트 JSX 반환 메서드
 *
 * @returns {JSX.Element} JSX
 */
export default function DevStackBox(): JSX.Element
{
	const categories: DevStackCategory[] = [ 'language', 'framework', 'database', 'server', 'etc' ];
	const { data, isLoading, isError } = useGetDevStacks();

	const getIcon = useCallback((category: DevStackCategory) =>
	{
		switch (category)
		{
			case 'language': return <Code className={cn('icon')} color='warning' />;
			case 'framework': return <AutoStories className={cn('icon')} color='warning' />;
			case 'database': return <Inventory className={cn('icon')} color='warning' />;
			case 'server': return <Storage className={cn('icon')} color='warning' />;
			default: return <DataObject className={cn('icon')} color='warning' />;
		}
	}, []);

	const getList = useCallback((category: DevStackCategory) => data?.filter((i) => category === i.category) || [], [ data ]);

	return (
		<Stack
			alignItems='center'
			data-component='DevStackBox'
			justifyContent='center'
			spacing={10}
			width='100%'
		>
			<Typography fontWeight='bold' letterSpacing={10} variant='h3'>개발스택</Typography>

			{isLoading ? <LottieText icon='sandclock' text='개발스택 로딩 중' /> : null}

			{isError ? <LottieText icon='error' text='개발스택을 가져올 수 없어요!' /> : null}

			{data ? categories.map((category) => (
				<Stack alignItems='center' justifyContent='center' key={category} spacing={4} width='100%'>
					<Stack alignItems='center' direction='row' justifyContent='center' spacing={2}>
						{getIcon(category)}
						<Typography fontWeight='bold' letterSpacing={8} variant='h4'>{category.toUpperCase()}</Typography>
					</Stack>

					<DevStackFeed stacks={getList(category)} />
				</Stack>
			)) : null}
		</Stack>
	);
}