/**
 * 쿼리 Template 컴포넌트
 *
 * @author RWB
 * @since 2023.07.28 Fri 01:51:50
 */

import { useToastState } from '@itcode/tools/store';

import Typography from '@mui/material/Typography';
import { DefaultOptions, QueryClient, QueryClientProvider, QueryClientProviderProps } from '@tanstack/react-query';
import { useMemo } from 'react';

export type QueryTemplateProps = Pick<QueryClientProviderProps, 'children'>

/**
 * 쿼리 Template 컴포넌트 JSX 반환 메서드
 *
 * @param {QueryTemplateProps} param0: QueryTemplateProps 객체
 *
 * @returns {JSX} JSX
 */
export default function QueryTemplate({ children }: QueryTemplateProps): JSX.Element
{
	const { setToast } = useToastState();

	const client = useMemo(() =>
	{
		const options = {
			onError: async (error: Response): Promise<void> =>
			{
				setToast({
					content: <Typography variant='caption'>API 오류가 발생했습니다.</Typography>,
					severity: 'error',
					title: `[${error.status}] 오류`
				});
			},
			retry: 0
		};

		const defaultOptions: DefaultOptions<Response> = {
			mutations: options,
			queries: {
				cacheTime: Infinity,
				refetchOnWindowFocus: false,
				staleTime: 3600 * 1000,
				...options
			}
		};

		return new QueryClient({ defaultOptions: defaultOptions as DefaultOptions<unknown> });
	}, [ setToast ]);

	return (
		<QueryClientProvider client={client} data-component='QueryTemplate'>
			{children}
		</QueryClientProvider>
	);
}