import Image from '@itcode/atom/Image';

import { useTheme } from '@mui/material';
import Box from '@mui/material/Box';
import ButtonBase, { ButtonBaseProps } from '@mui/material/ButtonBase';
import Stack from '@mui/material/Stack';
import Typography from '@mui/material/Typography';
import classNames from 'classnames/bind';
import { motion } from 'framer-motion';

import * as styles from './ProjectCard.module.scss';

const cn = classNames.bind(styles);

export interface ProjectCardProps extends ButtonBaseProps
{
	/**
	 * 타이틀
	 */
	title: string;

	/**
	 * 작성자
	 */
	author: string;

	/**
	 * 아이콘
	 */
	icon: string;

	/**
	 * 이미지 목록
	 */
	images: string[];

	/**
	 * 진행 날짜
	 */
	during?: number;

	/**
	 * 진행 중 여부
	 */
	isProcessing?: boolean;
}

/**
 * 프로젝트 카드 molecule 컴포넌트 JSX 반환 메서드
 *
 * @param {ProjectCardProps} param0: ProjectCardProps 객체
 *
 * @returns {JSX.Element} JSX
 */
export default function ProjectCard({ title, author, icon, images, during, isProcessing, ...props }: ProjectCardProps): JSX.Element
{
	const { palette: { warning: { main: warningMain }, success: { main: successMain } } } = useTheme();

	return (
		<ButtonBase className={cn('card')} data-component='ProjectCard' {...props}>
			<Stack height='100%' width='100%'>
				<Stack alignItems='center' direction='row' flex={1} padding={1} spacing={2} width='100%'>
					<img alt={icon} height={32} src={icon} title={title} width={32} />

					<Stack alignItems='start' flex={1}>
						<Typography fontWeight='bold' textAlign='start'>{title}</Typography>

						<Stack alignItems='center' direction='row' justifyContent='space-between' width='100%'>
							<Typography color='GrayText' variant='caption'>{author}</Typography>
							<Typography color={isProcessing ? warningMain : successMain} variant='caption'>{during}일 소요됨</Typography>
						</Stack>
					</Stack>
				</Stack>

				<Box className={cn('image')} overflow='hidden' position='relative' width='100%'>
					<Stack
						alignItems='center'
						className={cn('wrap')}
						height='100%'
						justifyContent='center'
						left={0}
						position='absolute'
						top={0}
						width='100%'
					>
						<Image alt={title} height='150%' src={images[0]} width='150%' />
					</Stack>

					<Stack
						alignItems='center'
						height='100%'
						justifyContent='center'
						left={0}
						position='absolute'
						top={0}
						width='100%'
					>
						<motion.div
							initial={{ scale: 0.8 }}
							transition={{ duration: 0.3 }}
							whileHover={{ scale: 1 }}
						>
							<Image
								alt={title}
								height='100%'
								src={images[0]}
								width='100%'
							/>
						</motion.div>
					</Stack>
				</Box>
			</Stack>
		</ButtonBase>
	);
}