/**
 * 진행 중 프로젝트 리스트 GraphQL 모듈
 *
 * @author RWB
 * @since 2023.08.16 Wed 02:10:03
 */

import { graphql, useStaticQuery } from 'gatsby';

/**
 * 진행 중 프로젝트 리스트 반환 메서드
 *
 * @returns {Queries.GetProceedProjectQuery} 진행 중 프로젝트 리스트
 */
export function useGetProceedProject(): Queries.GetProceedProjectQuery
{
	const query = useStaticQuery<Queries.GetProceedProjectQuery>(graphql`
	query GetProceedProject {
		allMarkdownRemark(
			sort: {frontmatter: {created: ASC}}
			filter: {frontmatter: {completed: {eq: null}}}
		) {
			edges {
				node {
					id
					frontmatter {
						title
						author
						subtitle
						languages
						images
						created
						completed
						links {
							url
								type
								name
							}
							icon
						}
					}
				}
			totalCount
		}
	}
	`);

	return query;
}