/**
 * 사이드바 molecule 컴포넌트
 *
 * @author RWB
 * @since 2023.08.07 Mon 19:07:38
 */

import pkg from '@itcode/package';
import { INFO } from '@itcode/tools/env';

import AutoStories from '@mui/icons-material/AutoStories';
import Code from '@mui/icons-material/Code';
import Home from '@mui/icons-material/Home';
import Box from '@mui/material/Box';
import Drawer, { DrawerProps } from '@mui/material/Drawer';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemButton from '@mui/material/ListItemButton';
import ListItemIcon from '@mui/material/ListItemIcon';
import ListItemText from '@mui/material/ListItemText';
import Stack from '@mui/material/Stack';
import Typography from '@mui/material/Typography';
import classNames from 'classnames/bind';
import { Link } from 'gatsby';
import { useCallback } from 'react';

import * as styles from './Sidebar.module.scss';

const cn = classNames.bind(styles);

export type SidebarProps = DrawerProps;

/**
 * 사이드바 molecule 컴포넌트 JSX 반환 메서드
 *
 * @param {SidebarProps} param0: SidebarProps 객체
 *
 * @returns {JSX.Element} JSX
 */
export default function Sidebar({ ...props }: SidebarProps): JSX.Element
{
	const handleClick = useCallback(() =>
	{
		// onClose 객체가 유효할 경우
		if (props.onClose)
		{
			props.onClose({}, 'backdropClick');
		}
	}, [ props.onClose ]);

	return (
		<Drawer data-component='Sidebar' sx={{ zIndex: 10001 }} {...props}>
			<Stack alignItems='center' height='100%' justifyContent='space-between'>
				<Box minWidth={250}>
					<List>
						<ListItem>
							<Link className='fullwidth' to='/' onClick={handleClick}>
								<ListItemButton>
									<ListItemIcon className={cn('icon')}>
										<Home />
									</ListItemIcon>

									<ListItemText primary='홈' />
								</ListItemButton>
							</Link>
						</ListItem>

						<ListItem>
							<Link className='fullwidth' to='/projects' onClick={handleClick}>
								<ListItemButton>
									<ListItemIcon className={cn('icon')}>
										<Code />
									</ListItemIcon>

									<ListItemText primary='프로젝트' />
								</ListItemButton>
							</Link>
						</ListItem>

						<ListItem>
							<Link className='fullwidth' to='/guestbook' onClick={handleClick}>
								<ListItemButton>
									<ListItemIcon className={cn('icon')}>
										<AutoStories />
									</ListItemIcon>

									<ListItemText primary='방명록' />
								</ListItemButton>
							</Link>
						</ListItem>
					</List>
				</Box>

				<Stack alignItems='center' marginBottom={4} spacing={1}>
					<Typography variant='caption'>{INFO.title}</Typography>
					<Typography color='GrayText' variant='caption'>{pkg.version}</Typography>
				</Stack>
			</Stack>
		</Drawer>
	);
}