/**
 * 프로젝트 모달 organism 컴포넌트
 *
 * @author RWB
 * @since 2024.04.26 Fri 16:17:20
 */

import Image from '@itcode/atom/Image';
import { useProjectState } from '@itcode/tools/store';
import { dateParse } from '@itcode/tools/util';

import AccessTimeIcon from '@mui/icons-material/AccessTime';
import GitHub from '@mui/icons-material/GitHub';
import LinkIcon from '@mui/icons-material/Link';
import Box from '@mui/material/Box';
import Divider from '@mui/material/Divider';
import List from '@mui/material/List';
import ListItemButton from '@mui/material/ListItemButton';
import ListItemIcon from '@mui/material/ListItemIcon';
import ListItemText from '@mui/material/ListItemText';
import Modal from '@mui/material/Modal';
import Stack from '@mui/material/Stack';
import Typography from '@mui/material/Typography';
import classNames from 'classnames/bind';
import { useCallback } from 'react';

import * as styles from './ProjectModal.module.scss';

const cn = classNames.bind(styles);

/**
 * 프로젝트 모달 organism 컴포넌트 반환 메서드
 *
 * @returns {JSX.Element} JSX
 */
export default function ProjectModal(): JSX.Element
{
	const { project, setProject } = useProjectState();

	const handleClose = useCallback(() =>
	{
		setProject(undefined);
	}, []);

	const getDateText = useCallback((num?: number | null) =>
	{
		// epoch가 유효할 경우
		if (num)
		{
			const { year, month, day } = dateParse(num);

			return `${year.text}-${month.text}-${day.text}`;
		}

		return undefined;
	}, []);

	return (
		<Modal data-component='ProjectModal' open={!!project} onClose={handleClose}>
			<Stack
				bgcolor='background.paper'
				borderRadius={2}
				boxShadow='0px 0px 30px #00000050'
				className={cn('modal')}
				gap={2}
				maxHeight='80%'
				maxWidth='md'
				overflow='scroll'
				padding={2}
				width='90%'
			>
				<Stack alignItems='center' padding={2}>
					<Box position='relative'>
						<Box borderRadius={4} boxShadow='0px 0px 20px #00000030' height='100%' overflow='hidden'>
							<Image height={128} src={project?.images?.[0] || ''} width={128} />
						</Box>

						<Box
							bgcolor='background.paper'
							borderRadius='50%'
							bottom={-10}
							boxShadow='0px 0px 20px #00000030'
							overflow='hidden'
							position='absolute'
							right={-10}
						>
							<Image height={32} src={project?.icon || ''} width={32} />
						</Box>
					</Box>

				</Stack>

				<Stack alignItems='center' gap={1} justifyContent='center'>
					<Typography color='primary.main' fontWeight='bold' variant='h6'>{project?.title}</Typography>

					<Typography>developed by {project?.author}</Typography>

					<Stack>
						{project?.subtitle?.map((i) => <Typography color='GrayText' key={i} variant='caption'>{i}</Typography>)}
					</Stack>

					<Stack alignItems='center' direction='row' gap={1}>
						<AccessTimeIcon fontSize='inherit' />
						<Typography variant='caption'>{getDateText(project?.created) || '미상'} ~ {getDateText(project?.completed) || '현재'}</Typography>
					</Stack>
				</Stack>

				<List>
					{project?.links?.map((props) => (props ? (
						<ListItemButton href={props.url || ''} key={props.url} target='_blank'>
							<ListItemIcon>
								{props.type === 'github' ? <GitHub /> : null}
								{props.type === 'web' ? <LinkIcon /> : null}
							</ListItemIcon>

							<ListItemText primary={props.name} secondary={props.type} />
						</ListItemButton>
					) : null))}
				</List>

				<Divider variant='fullWidth' />

				<Stack gap={2}>
					{project?.images?.map((i) => (i ? <Image key={i} src={i} width='100%' /> : null))}
				</Stack>
			</Stack>
		</Modal>
	);
}