import MetaTemplate from '@itcode/template/app/MetaTemplate/MetaTemplate';
import GuestbookTemplate from '@itcode/template/guestbook/GuestbookTemplate';

/**
 * 방명록 페이지 컴포넌트 JSX 반환 메서드
 *
 * @returns {JSX.Element} JSX
 */
export default function Guestbook(): JSX.Element
{
	return (
		<GuestbookTemplate />
	);
}

/**
 * 헤더 컴포넌트 JSX 반환 메서드
 *
 * @returns {JSX.Element} JSX
 */
export function Head(): JSX.Element
{
	return (
		<MetaTemplate title='방명록' url='/guestbook' />
	);
}