import { useTheme } from '@mui/material';
import IconButton, { IconButtonProps } from '@mui/material/IconButton';
import Tooltip from '@mui/material/Tooltip';
import classNames from 'classnames/bind';
import { Link } from 'gatsby';
import { useMemo } from 'react';

import * as styles from './LinkIconButton.module.scss';

const cn = classNames.bind(styles);

export interface LinkIconButtonProps extends IconButtonProps
{
	/**
	 * 툴팁 메시지
	 */
	tooltip?: string;

	/**
	 * 링크
	 */
	link: string;
}

/**
 * 링크 아이콘 버튼 molecule 컴포넌트 JSX 반환 메서드
 *
 * @param {FooterIconButtonProps} param0: LinkIconButtonProps 객체
 *
 * @returns {JSX.Element} JSX
 */
export default function LinkIconButton({ tooltip, link, ...props }: LinkIconButtonProps): JSX.Element
{
	const { palette: { mode } } = useTheme();

	const isInternal = useMemo(() => link.startsWith('/'), [ link ]);

	return (
		<Tooltip title={tooltip} arrow>
			{isInternal ? (
				<Link data-component='LinkIconButton' to={link}>
					<IconButton className={cn('icon', mode)} {...props} />
				</Link>
			) : (
				<a data-component='LinkIconButton' href={link} rel='noreferrer' target='_blank'>
					<IconButton className={cn('icon', mode)} {...props} />
				</a>
			)}
		</Tooltip>
	);
}