/**
 * 환영 박스 organism 컴포넌트
 *
 * @author RWB
 * @since 2023.08.05 Sat 04:22:07
 */

import LottieIcon from '@itcode/atom/LottieIcon';
import { INFO } from '@itcode/tools/env';
import { useSemantic } from '@itcode/tools/hook';

import Stack from '@mui/material/Stack';
import { Variant } from '@mui/material/styles/createTypography';
import Typography from '@mui/material/Typography';
import { Variants, motion } from 'framer-motion';
import { CSSProperties } from 'react';

/**
 * 환영 박스 organism 컴포넌트 JSX 반환 메서드
 *
 * @returns {JSX.Element} JSX
 */
export default function WelcomeBox(): JSX.Element
{
	const variants: Variants = {
		initial: {
			scaleY: 0,
			transition: { duration: 0.5 }
		},
		view: {
			scaleY: 1,
			transition: { duration: 0.5 }
		}
	};

	const variant = useSemantic<Variant>({ md: 'h4', xl: 'h3' });
	const width = useSemantic<CSSProperties['width']>({ sm: 128, xl: 256 });

	return (
		<Stack data-component='WelcomeBox'>
			<LottieIcon icon='confetti' width={width} />

			<motion.div
				initial='initial'
				transition={{ staggerChildren: 0.5 }}
				viewport={{ once: true }}
				whileHover='hover'
				whileInView='view'
			>
				<Stack overflow='hidden' padding={2} spacing={2} width='100%'>
					<motion.div variants={variants}>
						<Typography fontWeight='bold' variant={variant}>👋 Hello, There!</Typography>
					</motion.div>

					<motion.div variants={variants}>
						<Typography fontWeight='bold' variant={variant}><Typography color='dodgerblue' component='span' fontWeight='bolder' variant={variant}>{INFO.title}</Typography>에</Typography>
					</motion.div>

					<motion.div variants={variants}>
						<Typography fontWeight='bold' variant={variant}>방문해주신 <Typography color='hotpink' component='span' fontWeight='bolder' variant={variant}>당신</Typography>을</Typography>
					</motion.div>

					<motion.div variants={variants}>
						<Typography fontWeight='bold' variant={variant}>진심으로 환영합니다! 🎉</Typography>
					</motion.div>
				</Stack>
			</motion.div>
		</Stack>
	);
}