import ThemeSwitch from '@itcode/organism/global/ThemeSwitch';
import TopSwitch from '@itcode/organism/global/TopSwitch';
import { useThemeMode } from '@itcode/tools/hook';

import Box, { BoxProps } from '@mui/material/Box';
import Stack from '@mui/material/Stack';

export type InteractionPanelProps = Omit<BoxProps, 'children'>;

/**
 * 상호작용 패널 molecule 컴포넌트 JSX 반환 메서드
 *
 * @param {InteractionPanelProps} param0: InteractionPanelProps 객체
 *
 * @returns {JSX.Element}
 */
export default function InteractionPanel({ ...props }: InteractionPanelProps): JSX.Element
{
	const color = useThemeMode('#e0e0e0', '#2F2F2F');

	return (
		<Box bottom={0} data-component='InteractionPanel' padding={2} position='fixed' right={0} zIndex={10} {...props}>
			<Stack alignItems='center' color={color} direction='row' spacing={2}>
				<ThemeSwitch />

				<TopSwitch />
			</Stack>
		</Box>
	);
}