/**
 * 페이지 template 컴포넌트
 *
 * @author RWB
 * @since 2023.07.23 Sun 23:09:40
 */

import Footer from '@itcode/molecule/Footer';
import Navigation from '@itcode/organism/global/Navigation';

import Box from '@mui/material/Box';
import { WrapPageElementBrowserArgs } from 'gatsby';

/**
 * 페이지 template 컴포넌트 JSX 반환 메서드
 *
 * @param {WrapPageElementBrowserArgs} param0: WrapPageElementBrowserArgs 객체
 *
 * @returns {JSX.Element} JSX
 */
export default function PageTemplate({ element }: WrapPageElementBrowserArgs): JSX.Element
{
	return (
		<Box data-component='PageTemplate'>
			<Navigation />

			<Box component='section' data-component='PageSection'>
				{element}
			</Box>

			<Footer />
		</Box>
	);
}