/**
 * 개발 스택 카드 molecule 컴포넌트
 *
 * @author RWB
 * @since 2023.08.05 Sat 04:06:12
 */

import Image from '@itcode/atom/Image';
import { useSemantic } from '@itcode/tools/hook';

import { useTheme } from '@mui/material';
import Box, { BoxProps } from '@mui/material/Box';
import Stack from '@mui/material/Stack';
import Typography from '@mui/material/Typography';
import classNames from 'classnames/bind';
import { motion } from 'framer-motion';
import { CSSProperties } from 'react';

import * as styles from './DevStackCard.module.scss';

const cn = classNames.bind(styles);

export interface DevStackCardProps extends BoxProps
{
	/**
	 * 타이틀
	 */
	name: string;

	/**
	 * 이미지
	 */
	image: string;
}

/**
 * 개발 스택 카드 molecule 컴포넌트 JSX 반환 메서드
 *
 * @param {DevStackCardProps} param0: DevStackCardProps 객체
 *
 * @returns {JSX.Element} JSX
 */
export default function DevStackCard({ name, image, ...props }: DevStackCardProps): JSX.Element
{
	const { palette: { background: { default: defaultColor } } } = useTheme();

	const fontSize = useSemantic<CSSProperties['fontSize']>({ md: '1.5rem', xl: '2rem' });

	return (
		<Box className={cn('card')} data-component='DevStackCard' position='relative' width='100%' {...props}>
			<Box height='100%' left={0} position='absolute' top={0} width='100%'>
				<Image alt={name} height='100%' src={image} width='100%' />
			</Box>

			<motion.div
				initial='initial'
				whileHover='hover'
				variants={{
					hover: { opacity: 1 },
					initial: { opacity: 0 }
				}}
			>
				<Stack
					alignItems='center'
					height='100%'
					justifyContent='center'
					left={0}
					padding={2}
					position='absolute'
					top={0}
					width='100%'
					zIndex={2}
				>
					<Typography fontSize={fontSize} fontWeight='bold' textAlign='center'>{name}</Typography>
				</Stack>

				<Box
					bgcolor={defaultColor}
					className={cn('dimmer')}
					height='100%'
					left={0}
					padding={2}
					position='absolute'
					top={0}
					width='100%'
				/>
			</motion.div>
		</Box>
	);
}