/**
 * 편지 박스 organism 컴포넌트
 *
 * @author RWB
 * @since 2023.08.08 Tue 00:50:43
 */

import { AUTHOR, INFO } from '@itcode/tools/env';
import { useSemantic, useThemeMode } from '@itcode/tools/hook';
import { dateParse } from '@itcode/tools/util';

import Paper from '@mui/material/Paper';
import Stack from '@mui/material/Stack';
import Typography from '@mui/material/Typography';
import classNames from 'classnames/bind';
import { motion } from 'framer-motion';
import { Link } from 'gatsby';
import { CSSProperties } from 'react';

import * as styles from './LetterBox.module.scss';

const cn = classNames.bind(styles);

/**
 * 편지 박스 organism 컴포넌트 JSX 반환 메서드
 *
 * @returns {JSX.Element} JSX
 */
export default function LetterBox(): JSX.Element
{
	const bgcolor = useThemeMode<CSSProperties['backgroundColor']>('ivory', '#1E1E1E');
	const line = useThemeMode<string>('https://user-images.githubusercontent.com/50317129/260317028-9c42e25d-9213-4583-87af-66853cf16bc2.png', 'https://user-images.githubusercontent.com/50317129/260317030-e4b8575b-f09e-47f4-ab70-168a817268c6.png');
	const amount = useSemantic<'some' | number>({ md: 'some', xl: 0.3 });

	const date = dateParse();

	return (
		<Stack alignItems='center' data-component='LetterBox'>
			<motion.div
				initial={{ opacity: 0, translateY: 200 }}
				transition={{ duration: 1 }}
				viewport={{ amount, once: true }}
				whileInView={{ opacity: 1, translateY: 0 }}
			>
				<Paper>
					<Stack bgcolor={bgcolor} padding={4} spacing={2}>
						<Stack alignItems='center'>
							<img alt='decorate' className={cn('line')} src={line} />
						</Stack>

						<Stack alignItems='center' paddingBottom={4} paddingTop={4}>
							<img alt='decorate' height={100} src='https://user-images.githubusercontent.com/50317129/260320233-875aa1f9-115b-4fee-9cd8-5687cd1c14ec.png' width={100} />
						</Stack>

						<Stack>
							<Typography fontFamily='cafe24' fontSize='1.2rem'>반갑습니다!</Typography>
							<Typography fontFamily='cafe24' fontSize='1.2rem'>저는 <Typography color='dodgerblue' component='span' fontFamily='cafe24' fontSize='1.2rem' fontWeight='bold'>{INFO.title}</Typography>의 연구소장 <Typography color='dodgerblue' component='span' fontFamily='cafe24' fontSize='1.2rem' fontWeight='bold'>{AUTHOR.nickname}</Typography>입니다.</Typography>
							<Typography fontFamily='cafe24' fontSize='1.2rem'>당신의 방문을 진심으로 환영합니다!</Typography>
						</Stack>

						<Stack>
							<Typography fontFamily='cafe24' fontSize='1.2rem'>저희 연구소는 2019년도에 설립되어, {date.year.text}년 현재에 이르기까지 약 {date.year.value - 2019}년 동안 다양한 연구를 진행해왔습니다.</Typography>
							<Typography fontFamily='cafe24' fontSize='1.2rem'>주력 분야는 Full-Stack 기반의 웹개발입니다. 프론트엔드, 백엔드 뿐만 아니라, 웹 서비스를 배포하기 위한 WAS, DB와 같은 인프라 등. 웹 서비스를 구성하는 요소들의 연구에 중점을 두고 있습니다.</Typography>
							<Typography fontFamily='cafe24' fontSize='1.2rem'>이를 통해, 연구소 자체적으로 온전한 웹 서비스를 제공할 수 있는 역량을 보유하는 것이 저희의 목표입니다.</Typography>
							<Typography fontFamily='cafe24' fontSize='1.2rem'>또한, 데스크탑 앱, 모바일 등의 연구를 통해 웹을 넘어 다양한 가능성을 확보하고자 노력하고 있습니다.</Typography>
						</Stack>

						<Stack>
							<Typography fontFamily='cafe24' fontSize='1.2rem'>관람 중 저희 연구소에서 진행했거나, 진행 중인 프로젝트 모두를 확인할 수 있습니다.</Typography>
							<Typography fontFamily='cafe24' fontSize='1.2rem'>프로젝트의 개발일지를 확인하실 수 있으며, 원하실 경우 프로젝트의 특성에 따라 데모 혹은 소스코드 일체를 제공받을 수도 있습니다.</Typography>
							<Typography fontFamily='cafe24' fontSize='1.2rem'>지금 당장 프로젝트를 확인하고 싶으시다면, <Link to='/projects'><Typography color='dodgerblue' component='span' fontFamily='cafe24' fontSize='1.2rem'>대기중인 직원을 눌러 따라가주세요. 프로젝트가 마련된 공간으로 안내해드릴겁니다.</Typography></Link></Typography>
						</Stack>

						<Stack>
							<Typography fontFamily='cafe24' fontSize='1.2rem'>저희 연구소의 임직원들은 문의사항에 대해 가능한 최선의 답변을 드리도록 교육받고 있습니다.</Typography>
							<Typography fontFamily='cafe24' fontSize='1.2rem'>관람 간, 문의사항이 있다면 주저하지 말고 저희측에 전달해주세요.</Typography>
							<Typography fontFamily='cafe24' fontSize='1.2rem'>방명록을 활용해주시면, 저희 뿐만 아니라, 방문해주시는 모든 분들과 내용을 공유하실수도 있습니다.</Typography>
							<Typography fontFamily='cafe24' fontSize='1.2rem'>방명록을 구경하고 싶으시다면, <Link to='/guestbook'><Typography color='dodgerblue' component='span' fontFamily='cafe24' fontSize='1.2rem'>대기중인 직원을 눌러 따라가주세요. 방명록이 마련된 공간으로 안내해드릴겁니다.</Typography></Link></Typography>
							<Typography fontFamily='cafe24' fontSize='1.2rem'>해당 공간의 마지막에 저희와 소통할 수 있는 연락처에 대한 정보 일체를 마련해두었으니, 참고 부탁드립니다.</Typography>
						</Stack>

						<Stack>
							<Typography fontFamily='cafe24' fontSize='1.2rem'>당신의 방문이 저희에게 얼마나 큰 기쁨이 되는지 모르겠습니다.</Typography>
							<Typography fontFamily='cafe24' fontSize='1.2rem'>저희가 준비한 것들이 마음에 드셨으면 좋겠습니다.</Typography>
							<Typography fontFamily='cafe24' fontSize='1.2rem'>편안한 관람되시길 바랍니다.</Typography>
						</Stack>

						<Stack alignItems='end' paddingTop={8}>
							<Typography fontFamily='cafe24' fontSize='1.2rem'>{date.year.text}. {date.month.text}. {date.day.text}</Typography>
							<Typography fontFamily='cafe24' fontSize='1.2rem'>{INFO.title}장</Typography>
							<Typography fontFamily='cafe24' fontSize='1.2rem'>{AUTHOR.nickname} 드림</Typography>
							<img alt='sign' src='https://user-images.githubusercontent.com/50317129/260318801-82ab670b-e02e-4c1d-b254-d47a9762fdee.png' width={200} />
						</Stack>

						<Stack alignItems='center'>
							<img alt='decorate' className={cn('line', 'end')} src={line} />
						</Stack>
					</Stack>
				</Paper>
			</motion.div>
		</Stack>
	);
}