/**
 * 전체 프로젝트 리스트 GraphQL 모듈
 *
 * @author RWB
 * @since 2023.08.16 Wed 02:09:23
 */

import { graphql, useStaticQuery } from 'gatsby';

/**
 * 전체 프로젝트 리스트 반환 메서드
 *
 * @returns {Queries.GetAllProjectQuery} 전체 프로젝트 리스트
 */
export function useGetAllProject(): Queries.GetAllProjectQuery
{
	const query = useStaticQuery<Queries.GetAllProjectQuery>(graphql`
	query GetAllProject {
		allMarkdownRemark(sort: {fileAbsolutePath: DESC}) {
			edges {
				node {
					id
					frontmatter {
						title
						author
						subtitle
						languages
						images
						created
						completed
						links {
							url
							type
							name
						}
						icon
					}
				}
			}
			totalCount
		}
	}
	`);

	return query;
}