/**
 * 훅 모듈
 *
 * @author RWB
 * @since 2023.06.11 Sun 19:47:25
 */

import { useMediaQuery, useTheme } from '@mui/material';

export interface UseSemanticProps<T>
{
	/**
	 * xs 사이즈 (0px)
	 */
	xs?: T;

	/**
	 * sm 사이즈 (600px)
	 */
	sm?: T;

	/**
	 * md 사이즈 (900px)
	 */
	md?: T;

	/**
	 * lg 사이즈 (1200px)
	 */
	lg?: T;

	/**
	 * xl 사이즈 (1536px)
	 */
	xl?: T;
}

/**
 * 반응형에 따른 분기값 반환 훅 메서드
 *
 * @param {UseSemanticProps} 파라미터
 *
 * @returns {T} 반응형에 따른 파라미터
 */
export function useSemantic<T>({ xs, sm, md, lg, xl }: UseSemanticProps<T>): T | undefined
{
	const { breakpoints: { up } } = useTheme();

	const isSm = useMediaQuery(up('sm'));
	const isMd = useMediaQuery(up('md'));
	const isLg = useMediaQuery(up('lg'));
	const isXl = useMediaQuery(up('xl'));

	// 1536px 이상일 경우
	if (isXl)
	{
		return xl;
	}

	// 1200px 이상일 경우
	if (isLg)
	{
		return lg || xl;
	}

	// 900px 이상일 경우
	if (isMd)
	{
		return md || lg || xl;
	}

	// 600px 이상일 경우
	if (isSm)
	{
		return sm || md || lg || xl;
	}

	return xs || sm || md || lg || xl;
}

/**
 * 테마에 따른 분기값 반환 훅 메서드
 *
 * @param {T} light: 라이트모드 파라미터
 * @param {T} dark: 다크모드 파라미터
 *
 * @returns {T} 테마에 따른 파라미터
 */
export function useThemeMode<T>(light: T, dark: T): T
{
	const { palette: { mode } } = useTheme();

	return mode === 'light' ? light : dark;
}