import LottieIcon from '@itcode/atom/LottieIcon';
import { useGetAllProject } from '@itcode/graphql';
import ProjectFeed from '@itcode/organism/global/ProjectFeed';
import ProjectModal from '@itcode/organism/projects/ProjectModal';

import Box from '@mui/material/Box';
import Stack from '@mui/material/Stack';
import Typography from '@mui/material/Typography';

/**
 * 프로젝트 박스 organism 컴포넌트 JSX 반환 메서드
 *
 * @returns {JSX.Element} JSX
 */
export default function ProjectBox(): JSX.Element
{
	const list = useGetAllProject();

	return (
		<Box data-component='ProjectBox'>
			<Stack spacing={10}>
				<Stack alignItems='center' justifyContent='center' padding={2} spacing={4}>
					<LottieIcon icon='development' width={256} />

					<Stack alignItems='center' spacing={1}>
						<Typography color='dodgerblue' fontWeight='bold' textAlign='center' variant='h4'>저희가 진행한 프로젝트입니다!</Typography>
						<Typography color='skyblue' fontWeight='bold' textAlign='center' variant='h5'>관심있는 프로젝트를 눌러 개발일지를 확인해보세요.</Typography>
					</Stack>
				</Stack>

				<ProjectFeed list={list} />
			</Stack>

			<ProjectModal />
		</Box>
	);
}