import { useThemeState } from '@itcode/tools/store';

import { PaletteMode, ThemeProvider, createTheme } from '@mui/material';
import { PropsWithChildren, useCallback, useEffect } from 'react';

export type AppThemeProviderProps = PropsWithChildren;

/**
 * 앱 테마 제공자 organism 컴포넌트 JSX 반환 메서드
 *
 * @param {AppThemeProviderProps} param0: AppThemeProviderProps 객체
 *
 * @returns {JSX.Element} JSX
 */
export default function AppThemeProvider({ children }: AppThemeProviderProps): JSX.Element
{
	const { theme, setTheme } = useThemeState();

	const getTheme = useCallback((theme: PaletteMode) => createTheme({
		palette: { mode: theme },
		typography: { fontFamily: 'Pretendard, sans-serif' }
	}), []);

	useEffect(() =>
	{
		const saved = localStorage.getItem('theme') === 'light' ? 'light' : 'dark';

		setTheme(saved);
	}, [ setTheme ]);

	return (
		<ThemeProvider theme={getTheme(theme)}>
			{children}
		</ThemeProvider>
	);
}