import ProjectsProjectTemplate from '@itcode/template/projects/ProjectsProjectTemplate';
import ProjectsScreenerTemplate from '@itcode/template/projects/ProjectsScreenerTemplate';

import Box from '@mui/material/Box';

/**
 * 프로젝트 template 컴포넌트 JSX 반환 메서드
 *
 * @returns {JSX.Element} JSX
 */
export default function ProjectsTemplate(): JSX.Element
{
	return (
		<Box component='section' data-component='ProjectsTemplate'>
			<ProjectsScreenerTemplate />
			<ProjectsProjectTemplate />
		</Box>
	);
}