/**
 * 헤더 molecule 컴포넌트
 *
 * @author RWB
 * @since 2023.06.10 Sat 12:39:43
 */

import Logo from '@itcode/atom/Logo';
import { INFO } from '@itcode/tools/env';

import Menu from '@mui/icons-material/Menu';
import { useTheme } from '@mui/material';
import Box, { BoxProps } from '@mui/material/Box';
import IconButton from '@mui/material/IconButton';
import Stack from '@mui/material/Stack';
import Typography from '@mui/material/Typography';
import { useAnimate } from 'framer-motion';
import { Link } from 'gatsby';
import { MouseEventHandler, useEffect, useState } from 'react';

export interface HeaderProps extends BoxProps
{
	/**
	 * 메뉴 클릭 이벤트 핸들러
	 */
	onMenuClick?: MouseEventHandler<HTMLButtonElement>;
}

/**
 * 헤더 molecule 컴포넌트 JSX 반환 메서드
 *
 * @param {HeaderProps} param0: HeaderProps 객체
 *
 * @returns {JSX.Element} JSX
 */
export default function Header({ onMenuClick, ...props }: HeaderProps): JSX.Element
{
	const { palette: { background, text } } = useTheme();

	const [ ref, animate ] = useAnimate<HTMLDivElement>();

	const [ isHideState, setHideState ] = useState(false);
	const [ isTopState, setTopState ] = useState(true);

	useEffect(() =>
	{
		const handle = ({ deltaY }: WheelEvent): void =>
		{
			setHideState(deltaY > 0);
		};

		// document가 유효할 경우
		if (document)
		{
			window.addEventListener('wheel', handle);
		}

		return () =>
		{
			// document가 유효할 경우
			if (window)
			{
				window.removeEventListener('wheel', handle);
			}
		};
	}, [ ref.current, setHideState ]);

	useEffect(() =>
	{
		const handle = (): void =>
		{
			setTopState(window.scrollY < 1);
		};

		// document가 유효할 경우
		if (document)
		{
			document.addEventListener('scroll', handle);
		}

		return () =>
		{
			// document가 유효할 경우
			if (document)
			{
				document.removeEventListener('scroll', handle);
			}
		};
	}, [ ref.current, setTopState ]);

	useEffect(() =>
	{
		if (ref.current)
		{
			animate(ref.current, { top: isHideState ? -ref.current.scrollHeight : 0 }, { duration: 0.3 });
		}
	}, [ isHideState, ref.current, animate ]);

	return (
		<Box
			bgcolor={isTopState ? '#00000000' : background.default}
			borderBottom={isTopState ? '1px solid' : 'none'}
			borderColor={isTopState ? '#DDDDDD44' : undefined}
			boxShadow={isTopState ? undefined : '0px 3px 10px #00000022'}
			color={isTopState ? 'white' : text.primary}
			component='header'
			data-component='Header'
			height={50}
			left={0}
			position='fixed'
			ref={ref}
			top={0}
			width='100%'
			zIndex={10000}
			{...props}
		>
			<Stack alignItems='center' direction='row' height='100%' padding={2} spacing={2}>
				{onMenuClick ? (
					<IconButton color='inherit' onClick={onMenuClick}>
						<Menu />
					</IconButton>
				) : null}

				<Link to='/'>
					<Stack alignItems='center' direction='row' spacing={2}>
						<Logo height={24} width={24} />
						<Typography color='inherit' fontWeight='bold'>{INFO.title}</Typography>
					</Stack>
				</Link>
			</Stack>
		</Box>
	);
}