import { DevStacks } from '@itcode/tools/env';

import { useQuery, UseQueryResult, UseQueryOptions } from '@tanstack/react-query';

export const QUERY_KEY = {
	DEVSTACK: 'devstack',
	SCREENER_VIDEO: 'screener-video'
};

/**
 * 스크리너 비디오 반환 API 메서드
 *
 * @param {UseQueryOptions} options: UseQueryOptions 객체
 *
 * @returns {UseQueryResult} UseQueryResult 객체
 */
export function useScreenerVideo(options?: UseQueryOptions<string[], Response>): UseQueryResult<string[], Response>
{
	return useQuery<string[], Response>([ QUERY_KEY.SCREENER_VIDEO ], async () =>
	{
		const response = await fetch('https://datastore.itcode.dev/itcode/video');

		// 정상 응답일 경우
		if (response.ok)
		{
			const json = await response.json();

			return json as string[];
		}

		throw response;
	}, options);
}

/**
 * 개발 스택 반환 API 메서드
 *
 * @param {UseQueryOptions} options: UseQueryOptions 객체
 *
 * @returns {UseQueryResult} UseQueryResult 객체
 */
export function useGetDevStacks(options?: UseQueryOptions<DevStacks[], Response>): UseQueryResult<DevStacks[], Response>
{
	return useQuery<DevStacks[], Response>([ QUERY_KEY.DEVSTACK ], async () =>
	{
		const response = await fetch('https://datastore.itcode.dev/itcode/image');

		// 정상 응답일 경우
		if (response.ok)
		{
			const json = await response.json();

			return json as DevStacks[];
		}

		throw response;
	}, options);
}