import InteractionPanel from '@itcode/molecule/InteractionPanel';
import AppThemeProvider from '@itcode/organism/global/AppThemeProvider';
import DevtoolDefender from '@itcode/organism/global/DevtoolDefender';
import Toaster from '@itcode/organism/global/Toaster';
import QueryTemplate from '@itcode/template/app/QueryTemplate';

import Box from '@mui/material/Box';
import CssBaseline from '@mui/material/CssBaseline';
import { WrapRootElementBrowserArgs } from 'gatsby';
import ReactGA from 'react-ga4';

/**
 * 루트 template 컴포넌트 JSX 반환 메서드
 *
 * @param {WrapRootElementBrowserArgs} param0: WrapRootElementBrowserArgs 객체
 *
 * @returns {JSX.Element} JSX
 */
export default function RootTemplate({ element }: WrapRootElementBrowserArgs): JSX.Element
{
	ReactGA.initialize('G-ZH2SCZ2RB2');

	return (
		<DevtoolDefender>
			<QueryTemplate>
				<AppThemeProvider>
					<Box component='main' data-component='RootTemplate'>
						<CssBaseline />

						{element}

						<InteractionPanel />
						<Toaster />
					</Box>
				</AppThemeProvider>
			</QueryTemplate>
		</DevtoolDefender>
	);
}