import GuestbookCommentTemplate from '@itcode/template/guestbook/GuestbookCommentTemplate';
import GuestbookScreenerTemplate from '@itcode/template/guestbook/GuestbookScreenerTemplate';

import Box from '@mui/material/Box';

/**
 * 방명록 template 컴포넌트 JSX 반환 메서드
 *
 * @returns {JSX.Element} JSX
 */
export default function GuestbookTemplate(): JSX.Element
{
	return (
		<Box component='section' data-component='GuestbookTemplate'>
			<GuestbookScreenerTemplate />
			<GuestbookCommentTemplate />
		</Box>
	);
}