import LottieIcon, { LottieIconType } from '@itcode/atom/LottieIcon';

import Stack, { StackProps } from '@mui/material/Stack';
import Typography from '@mui/material/Typography';

export interface LottieTextProps extends Omit<StackProps, 'children'>
{
	/**
	 * 아이콘
	 */
	icon: LottieIconType;

	/**
	 * 텍스트
	 */
	text: string;
}

/**
 * 로티 텍스트 molecule 컴포넌트 JSX 반환 메서드
 *
 * @param {LottieTextProps} param0: LottieTextProps 객체
 *
 * @returns {JSX.Element} JSX
 */
export default function LottieText({ icon, text, ...props }: LottieTextProps): JSX.Element
{
	return (
		<Stack alignItems='center' data-component='LottieText' spacing={2} {...props}>
			<LottieIcon height={128} icon={icon} width={128} />

			<Typography>{text}</Typography>
		</Stack>
	);
}