import MetaTemplate from '@itcode/template/app/MetaTemplate/MetaTemplate';
import ProjectsTemplate from '@itcode/template/projects/ProjectsTemplate';

/**
 * 프로젝트 페이지 컴포넌트 JSX 반환 메서드
 *
 * @returns {JSX.Element} JSX
 */
export default function Projects(): JSX.Element
{
	return (
		<ProjectsTemplate />
	);
}

/**
 * 헤더 컴포넌트 JSX 반환 메서드
 *
 * @returns {JSX.Element} JSX
 */
export function Head(): JSX.Element
{
	return (
		<MetaTemplate title='프로젝트' url='/projects' />
	);
}